import Get from "@/common/get.js"
import Post from "@/common/post.js"


/* 
 获取订单列表
 path：/api/order/list
 method:get
 body:{
	 非必填
	 type：------订单状态
	 page------分页参数起始值（默认从0查询所有）
	 limit------分页数步长值
	 search-----搜索订单编号
 }
 data{
	 add_time-----下单时间
	 seckill_id------秒杀产品编号
	 bargain_id------砍价产品编号
	 combination_id------拼团产品编号
	 id------订单编号
	 order_id-----订单号
	 pay_price----支付金额
	 total_num-----订单商品总数
	 total_price----订单总价
	 pay_postage-----支付邮费
	 total_postage----邮费
	 paid-----支付状态|1=已支付，0=未支付
	 status-----订单状态|0=待发货，1=待收货，2=待评价，3=已完成
	 refund_status-----退款状态|0=未退款，1=申请中，2=已退款
	 pay_type-----支付方式
	 coupon_price-----优惠券金额
	 deduction_price-----抵扣金额
	 pink_id----拼团那编号
	 delivery_type-------发货类型|send=送货，express=发货
	 is_del--------用户订单是否删除|1=是，0=否
	 cartInfo{------购物车信息
		 uid-----用户编号
		 id---购物车编号
		 type----产品类型（默认product）
		 peoduct_id----普通产品编号
		 product_attr_unique-------产品属性
		 cart_num----购物车数量
		 add_time-----添加购物车时间
		 is_pay-----是否下单|1=是，0=否
		 is_del-----是否删除|0=是，1=否
		 is_new-----购买方式|1=直接购买，0=购物车购买
		 seckill_id------秒杀产品编号
		 bargain_id------砍价产品编号
		 combination_id------拼团产品编号
		 productInfo{----购物车产品详情信息
			 id---产品id
			 image-----产品图片
			 slider_image---产品轮播图
			 price-------产品售价
			 ot_prict---市场价
			 vip_price------会员价
			 postage------是否包邮|1=是，0=否
			 mer_id-------商户编号
			 give_integral------赠送积分
			 cate_id-----产品分类编号
			 sales-------销量
			 stock-----库存
			 store_name----名称
			 store_info-----简介
			 unit_name------单位
			 is_show-----是否显示|1=显示，0=隐藏
			 is_del-----是否删除|1=隐藏，0=显示
			 is_postage-----是否包邮|1=是，0=否
			 cost-----产品成本
			 attrInfo{-----产品属性
				 product_id------产品编号
				 suk------产品属性索引值
				 stock------产品属性库存
				 sales------产品属性销量
				 price------产品属性价格
				 image------产品属性图片
				 unique------产品属性唯一值
				 cost------产品属性成本价
			 }
		 }
		 truePrice------购物车产品支付金额
		 vip-truePrice------购物车产品会员支付金额
		 trueStock------物车产品库存
		 costPrice------物车产品成本价
		 unique------购物车和订单号MD5加密的唯一值
		 is_reply------是否评价|1=是，0=f 
	 }
	 _status{-----订单状态信息
	 			 _type------订单状态
	 			 _title------订单的状态标题
	 			 _msg------订单的状态说明
	 			 _class------订单的状态样式
	 			 _payType------订单支付方式
	 			 -deliveType------订单的发货类型
	 }
	 _pay_time------支付时间
	 _add_time------下单时间
	 _status_pic------订单的状态图片
 }
 */
export function orderList(data){
	return Get.urlRequest("/api/order/list",data)
}



/* 
订单创建
path：/api/ordere/create/:key
method:post
路径参数：
key-------订单信息缓存标识
 body{
	 必须参数
	 addressId----地址编号
	 couponId----领取优惠券编号
	 payType----支付方式
	 mark------订单备注
	 非必须
	 useIntegral----是否积分抵扣
	 combinationId-----砍价产品编号
	 pinkId--------拼团编号
	 seckill_id-----秒杀产品编号
	 formId-----表单id（小程序模板消息使用）
	 bargainId------砍价产品编号
	 from-----支付类型
 }
 data{
	 status----状态
	 result{
		 orderId----订单编号
		 key----购物车缓存key
		 jsConfig{------微信支付参数
			 appId----appId
			 nonceStr----随机字符串
			 package-----统一下单接口返回的prepay_id参数值
			 signType------签名类型
			 paySign------签名
			 timestamp----时间戳
		 }
		 
	 }
 }
 
 */
export function orderCreate(key,data){
	return Post.urlRequest("/api/order/create/"+key,data)
}


